import itemList from "component/item-list.vue"
export default {
	components: {
		itemList
	},
	data() {
		return {
			/* 商家列表 */
			shopsList: [{}, {}, {}, {}, {}]
		}
	},
	onReachBottom() {
		uni.showLoading({
			title: '加载中',
			mask: true
		})
		setTimeout(function() {
			uni.hideLoading()
		}, 1000);
	},
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	methods: {
		/* 单个编辑 */
		unsubcribe(item, index) {
			let _this = this
			uni.showModal({
				title: '确认要取消关注这家店铺吗？',
				content: '店铺名',
				success: function(res) {
					if (res.confirm) {
						_this.shopsList.splice(index, 1)
						uni.showToast({
							title: '操作成功',
							icon: 'none'
						});
					}
				}
			});
		},
		/* 页面跳转 */
		jump2shopsDetail(item, index) {
			uni.navigateTo({
				url: '/pages/shops/detail'
			})
		},
	}
}
